.poem {
    font-family: "Calibri", sans-serif;
    color: rgb(225, 225, 225);
    text-align: center;
    margin: auto;
    display: flex;
    width: fit-content;
    .text-wrapper {
        p {
            font-size: 18px;
            text-align: left;
            font-weight: 50;
            margin-top: 0;
            margin-bottom: 0.5rem;
            margin-left: .75rem;
        }
        h1 {
            font-size: 36px;
            text-align: left;
            margin-bottom: 2rem;
        }
    }
  
}


